@import "../../style/variables.scss";

.app {
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    font-weight: bold;
    font-size: 28px;
    line-height: 37px;
    div {
      color: $main-color;
    }
  }
  &__logo {
    width: 200px;
  }
  &__menu {
    ul {
      display: flex;
      justify-content: space-between;
      font-weight: bold;
      font-size: 24px;
      line-height: 32px;
      text-transform: uppercase;
      li {
        margin: 0 8px;
        a {
          transition: all 0.6s;
        }
        &:hover {
          color: $main-color;
        }
      }
    }
  }
}
